body {
  line-height: 1.5;
  color: #393939;
  background-color: #fff;
  font-family: "M PLUS Rounded 1c", sans-serif;
  font-size: 1rem;
  font-weight: 500;
}

.p-concept {
  position: relative;
  padding-top: 6.9375rem;
  overflow: hidden;
}
@media screen and (min-width: 768px) {
  .p-concept {
    padding-top: 14.0625vw;
    padding-bottom: 8.4375vw;
  }
}

.p-concept::before {
  position: absolute;
  content: "";
  background-position: top left;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url(".././images/common/concept-bg__sp.png");
  width: 100%;
  height: min(82.4vw, 28.125rem);
  top: 0;
  left: 0.75rem;
  z-index: -5;
}
@media screen and (min-width: 768px) {
  .p-concept::before {
    background-image: url(".././images/common/concept-bg__pc.png");
    left: 32.8125vw;
    height: 100%;
    background-size: contain;
  }
}

@media screen and (min-width: 768px) {
  .p-concept__wrap {
    display: flex;
    align-items: center;
    justify-content: start;
    flex-direction: row-reverse;
    gap: 7.421875vw;
  }
}

.p-concept__body {
  padding: 0 1.25rem;
}
@media screen and (min-width: 768px) {
  .p-concept__body {
    width: 31.796875vw;
    padding: 0;
  }
}

.p-concept__en-title {
  color: #1391e6;
  font-size: 0.6875rem;
  font-weight: 700;
  letter-spacing: 1.4px;
  text-transform: uppercase;
}
@media screen and (min-width: 768px) {
  .p-concept__en-title {
    font-size: 1.09375vw;
  }
}

.p-concept__ja-title {
  font-size: 1.25rem;
  font-weight: 700;
  line-height: 1.6;
  letter-spacing: 0.08em;
  margin-top: 0.875rem;
}
@media screen and (min-width: 768px) {
  .p-concept__ja-title {
    font-size: 2.1875vw;
    margin-top: 2.8125vw;
    letter-spacing: 0.00625vw;
  }
}

.p-concept__text {
  font-size: 0.875rem;
  line-height: 1.7;
  margin-top: 1.6875rem;
}
@media screen and (min-width: 768px) {
  .p-concept__text {
    margin-top: 2.8125vw;
    font-size: 1.09375vw;
  }
}

.p-concept__btn {
  display: flex;
  justify-content: center;
  margin-top: 2.3125rem;
}
@media screen and (min-width: 768px) {
  .p-concept__btn {
    justify-content: start;
    margin-top: 2.890625vw;
  }
}

.p-concept__img {
  width: 100%;
  max-width: min(89.0666666667vw, 28.125rem);
  aspect-ratio: 334/230;
  margin-top: 2.5625rem;
  margin-right: auto;
}
@media screen and (min-width: 768px) {
  .p-concept__img {
    max-width: 50vw;
    aspect-ratio: 640/438;
    margin: 0;
  }
}

.p-concept__img,
.p-concept__img img {
  border-radius: 0 1.5625vw 1.5625vw 0;
}